import Vue from 'vue';
import VueRouter from 'vue-router';
//

import FrontIndex from '@/components/FrontIndex';
//import FrontHeader from '@/components/FrontHeader';
import Page from '@/components/pages/Page';
import Menu  from '@/components/pages/Menu';
import CHild1 from '@/components/pages/Child';
import CHild2 from '@/components/pages/Child2';
import CHild3 from '@/components/pages/Child3';

import BackLogin from '@/components/pages/BackLogin';
import BackDashboard from '@/components/BackDashboard';
import BackProducts from '@/components/BackProducts';
import BackOrders from '@/components/BackOrders';
import BackCoupons from '@/components/BackCoupons';
import BackCustomerOrder from '@/components/BackCustomerOrder';
import BackCustomerCheckOut from '@/components/BackCustomerCheckOut';

import FrontMarketing from '@/components/FrontMarketing';
import FrontMemberCart from '@/components/FrontMemberCart';
import FrontMemberCheckOut from '@/components/FrontMemberCheckOut';

Vue.use(VueRouter);

export default new VueRouter({
    routes:[
        {            
            path:'*',//*
            redirect:'index',//
        },
        {
            name:'',//
            path:'/index',//
            component:FrontIndex,//
            children:[
                {
                    name:'',//
                    path:'',//
                    component:FrontMarketing,//      
                },
                // {
                //     name:'',//
                //     path:'/trainer',//
                //     component:FrontTeacher,//      
                // },
                // {
                //     name:'',//
                //     path:'/divider',//
                //     component:FrontDivider,//      
                // },
                {
                    name:'',//
                    path:'membercart',//
                    component:FrontMemberCart,//      
                },
                {
                    name:'',//
                    path:'membercart/:productid',//
                    component:FrontMarketing,//      
                },
                {
                    name:'',//
                    path:'membercheckout/:orderId',//
                    component:FrontMemberCheckOut,//      
                },
            ],            
        },
        {
            name:'',//
            path:'',//
            component:FrontIndex,//
            children:[
                {
                    name:'',//
                    path:'',//
                    component:FrontMarketing,//       
                },
                // {
                //     name:'',//
                //     path:'/trainer',//
                //     component:FrontTeacher,//      
                // },
                // {
                //     name:'',//
                //     path:'/divider',//
                //     component:FrontDivider,//      
                // },
                {
                    name:'',//
                    path:'membercart',//
                    component:FrontMemberCart,//      
                },
                {
                    name:'',//
                    path:'membercart/:productid',//
                    component:FrontMarketing,//      
                },
                {
                    name:'',//
                    path:'membercheckout/:orderId',//
                    component:FrontMemberCheckOut,//      
                },
            ],            
        },
        {
            name:'login',//
            path:'/login',//
            component:BackLogin,//
        },
        {
            path:'/page',//
            //component:Page,//
            components:{
                default:Page,
                menu:Menu,
                meta:{requiresAuth : true},
            },
            children:[
                {
                    name:'1',//
                    path:'',//
                    component:CHild1,//
                    meta:{requiresAuth : true},
                },        
                {
                    name:'2',//
                    path:'child2',//
                    component:CHild2,//
                    meta:{requiresAuth : true},
                },
                {
                    name:'3',//
                    path:'child/:id',//
                    component:CHild3,//
                    meta:{requiresAuth : true},
                },
            ],
        },
        {
            name:'dashboard',//
            path:'/admin',//
            component:BackDashboard,//
            meta:{requiresAuth : true},            
            children:[
                {
                    name:'productsdefault',//
                    path:'',//
                    component:BackProducts,//
                    meta:{requiresAuth : true},
                }, 
                {
                    name:'products',//
                    path:'products',//
                    component:BackProducts,//
                    meta:{requiresAuth : true},
                },
                {
                    name:'orders',//
                    path:'orders',//
                    component:BackOrders,//
                    meta:{requiresAuth : true},
                },
                {
                    name:'coupons',//
                    path:'coupons',//
                    component:BackCoupons,//
                    meta:{requiresAuth : true},
                },

            ],
        },
        {
            name:'dashboard',//
            path:'/',//
            component:BackDashboard,//
            meta:{requiresAuth : true},            
            children:[
                {
                    name:'customerorder',//
                    path:'customerorder',//
                    component:BackCustomerOrder,//       
                },  
                {
                    name:'customercheckout',//
                    path:'customercheckout/:orderId',//
                    component:BackCustomerCheckOut,//       
                }, 
            ],
        },

   
    ],
});


